import disciplines_lang_it from '../../../res/locales/it/viewer-aec-disciplines.loc-generated.json';
import disciplines_lang_ja from '../../../res/locales/ja/viewer-aec-disciplines.loc-generated.json';
import disciplines_lang_ko from '../../../res/locales/ko/viewer-aec-disciplines.loc-generated.json';
//import disciplines_lang_nl from '../../../res/locales/nl/viewer-aec-disciplines.loc-generated.json';
import disciplines_lang_pl from '../../../res/locales/pl/viewer-aec-disciplines.loc-generated.json';
